* {
  box-sizing: border-box;
}

ul {
  list-style: none;
}

img {
  width: 18rem;
  height: 21rem;
  margin-right: .5rem;

}

a {
  text-decoration: none;
}

.card {
  margin-bottom: .5rem;
  margin-top: .5rem;
}

.title {
  display: inline;
}

.left {
  display: flex;
}

.right {
  display: flex;
}

.form-control {
  height: 2rem;
}

.profile-body {
  height: fit-content;
}

.profile-nav {
  text-align: right;
  margin-right: 1rem;
  margin-top: .5rem;
}

.profile-nav button {
  margin-right: .5rem;
}

.recipe-list-img {
  width: 14rem;
  height: 12rem;
}

.recipe-list-body {
  display: inline-block;
}
